.transfer-form-admin {
    display: flex;
}

.box {
    display: flex;
    flex-direction: column;
    padding: 1rem;
    padding-top: .25rem;
    /* border: 0.25rem inset white; */
}

.box h2,p {
    margin: 0;
    margin-bottom: 0;
}

.transfer-header {
    text-align: center;
    margin: 0;
}

.transfer-form {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.transfer-form legend {
    border-top: .25rem solid gray;
    padding: 1rem;
}

.transfer-submit-button {
    width: min-content;
    align-self: center;
    margin-top: 1rem;
}

.transfer-form-admin h2 {
    font-family: 'Montserrat';
    margin-bottom: 1rem;
}

.transfer-form p {
    margin-bottom: .5rem;
}

.username-error {
    color: red;
    font-size: 0.75rem;
}

.username-accept {
    color: green;
    font-size: 0.75rem;
}

.deposit-form, .withdraw-form {
    display: flex;
    flex-direction: column;
    padding: 1rem;
    align-items: center;
}

.deposit-form h1, .withdraw-form h1 {
    margin: 0;
    margin-bottom: .25rem;
}

.deposit-form p, .withdraw-form p {
    margin-bottom: 1rem;
    margin-top: 1rem;
}

.deposit-form input[type="number"], .withdraw-form input[type="number"] {
    width: 25%;
    align-self: center;
    text-align: right;
    padding: .12rem;
}

.deposit-form input[type="text"], .withdraw-form input[type="text"] {
    width: 40%;
    height: 1rem;
    align-self: center;
    text-align: center;
}

.deposit-inputbox, .withdraw-inputbox {
    display: flex;
    justify-content: space-between;
}

.deposit-form legend, .withdraw-form legend {
    border-top: 0.25rem solid gray;
    position: relative;
}

.username-error.deposit, .username-error.withdraw {
    position: absolute;
    right: 5%;
    top: 41%;
}

.username-accept.deposit, .username-accept.withdraw {
    position: absolute;
    right: 12.5%;
    top: 33%;
}